<template>
  <v-container>
    <v-layout>
      <v-flex xs10 offset-xs1 md4 offset-md4>
        <v-card>
          <v-card-title><span class="title"></span></v-card-title>
          <v-card-text>
          <v-form ref="form" v-model="valid">
            <v-text-field
            v-model="model.firstname" 
            label="" 
            :rules="defaultRule('login')"
            required></v-text-field>
            <v-text-field
            v-model="model.lastname" 
            label="" 
            :rules="defaultRule('lname')"
            required></v-text-field>
            <v-text-field 
            v-model="model.patronymic" 
            label=""></v-text-field>
            <v-text-field 
            v-model="model.email" 
            label="Email"
            :rules="[...defaultRule('email'),emailRule]"></v-text-field>
            <v-text-field 
            v-model="model.userName"
            label="" 
            :rules="defaultRule('login')"
            required></v-text-field>
            <v-text-field
            v-model="model.password"
            label="" 
            :rules="[...defaultRule('pass'), passRule]"
            required type="password"></v-text-field>
            <v-text-field
            label=" "
            :rules="confirmPassRule"
            required type="password"></v-text-field>
          </v-form>
          </v-card-text>
          <v-card-actions>
            <v-layout row justify-end>
              <v-btn @click="sendData()" color="primary">OK</v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>   
  </v-container>
</template>

<script>
  import onResize from "../mixins/on-resize"
  import formValidation from "../mixins/form-validation"
  export default {
    mixins:[onResize, formValidation],
    data(){
        return{
          valid:false,
          model:{
            email:"",
            password:"",
            firstname:"",
            lastname:"",
            userName:"",
            patronymic:""
          },
          rules:{
            login:{first:"  ",second:"    "},
            pass:{first:"  ",second:"    "},
            fname:{first:"  ",second:"    "},
            lname:{first:"  ",second:"    "},
            email:{first:"  email",second:"Email    "},
          },
          confirmPassRule:[v=> v === this.model.password || "  "],
          emailRule:v => /.+@.+/.test(v) || ' email',
          passRule:v => (v || '').length >= 6 ||
              `    6 `
        }
    },
    methods:{
      defaultRule(label){
        var e = this.rules[label];
        return [v=>!!v||e.first, v=>!/^.*\s.*$/.test(v) ||e.second];
      },
      sendData(){
        if(!this.valid)
          return;
        this.$http.post(`/api/account/register`, this.model).then(
          response=>{
            this.$router.push({name:"afterRegister"});
          },
          response=>{
          }
        )
      }
    }
  }
</script>



